import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      createLoading: false,
      isBrand: true
    }
  },
  computed: {
    ...mapGetters('user', ['isAuth', 'userInfo'])
  },
  props: {
    recordLog: {
      type: Object,
      default: () => {
        return {}
      }
    },
    record: {
      type: Object,
      default: () => {
        return {}
      }
    },
    hasRecord: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    setCreateShow () {
      this.$emit('show')
    },
    showAuthDialog () {
      uni.showModal({
        title: '提示',
        content: '打卡需要绑定用户信息, 是否前往认证',
        success (res) {
          if (res.confirm) {
            qq.reLaunch({ url: '/pages/user/login' })
          } else {
            qq.navigateBack()
          }
        }
      })
    }
  }
}
